import React, { Component } from 'react';
import Button from '@material-ui/core/Button';
import { Link } from 'react-router-dom';
import customStyle from './register.module.css';
import axios from '../../axiosConfig';
const Header = React.lazy(() => import('../header/header'));
const Footer = React.lazy(() => import('../footer/footer'));
const InputField = React.lazy(() => import('../../admin/views/TextFields/InputField'));
class Register extends Component {
    state = { 
        prevUrl: localStorage.getItem('curUrl'),
        firstName: '',
        lastName: '',
        email: '',
        mobile: '',
        password: ''
     }

     onChange = (event) => {
         this.setState({
             [event.target.name] : event.target.value
         })
     }
     signUp = (event) => {
         event.preventDefault();
         const params = {
             firstName: this.state.firstName,
             lastName: this.state.lastName,
             email: this.state.email,
             mobile: this.state.mobile,
             password: this.state.password
         }
         axios.post('userRegister', params)
         .then(res => {
             alert(res.data['msg']);
             if (res.data['code'] === 1) {
                 this.props.history.push(this.state.prevUrl)
             }
         })
         .catch(function(res) {
             console.log(res);
         })
     }
    render() { 
        return (  
            <div className="App bg-white">
                <Header />
                <div className="body">
                    <div className="container-fluid mt-5">
                        <div className="row justify-content-center">
                            <div className="col-12 col-sm-4 p-5 border rounded">
                                <h4 className="text-center">Login with Fashion Focus</h4>
                                <div className="row no-gutters mb-3 mt-3">
                                    <div className="col-sm-6">
                                        <Link to="/login" >
                                            <button className={customStyle.login}>LOGIN</button>
                                        </Link>
                                    </div>
                                    <div className="col-sm-6">
                                        <Link  >
                                            <button  className={customStyle.register}>REGISTER</button>
                                        </Link>
                                    </div>
                                </div>
                                <form onChange={this.onChange}>
                                    <div className="row">
                                        <div className="col-sm-6">
                                        <InputField name="firstName" label="First Name" />
                                        </div>
                                        <div className="col-sm-6">
                                        <InputField name="lastName" label="Last Name" />
                                        </div>
                                    </div>
                                    <InputField name="email" label="Email" />
                                    <InputField name="mobile" label="Mobile" />
                                    <InputField name="password" label="Password" />
                                    <Button onClick={this.signUp} variant="contained" color="primary" className="mt-3 p-2 w-100">Register</Button>
                                </form>
                            </div>
                        </div>
                    </div>
                </div>
                <Footer />
            </div>
        );
    }
}
 
export default Register;